const uni = require('uni').default;





































import {
	mapState,
	mapMutations
} from 'vuex'
var badgeView;
export default {
	data() {
		return {
			itemList: [
				'../../static/banner1.jpg',
				'../../static/banner2.jpg',
				'../../static/banner3.jpg'
			],
			indicatorDots: true,
			autoplay: true,
			interval: 5000,
			duration: 1000,
			
			city:'杭州'
		}
	},
	created:function(){
		this.init();
	},
	computed: {
		...mapState(['badgeCount']),
		...mapState(['badgeView']),
		...mapState(['gcolor'])
	},
	methods:{
		...mapMutations(['setbadgeCount']),
		...mapMutations(['setbadgeView']),
		init(){
			console.log(this.gcolor)

























			

			wx.setTabBarBadge({
			index: 2,
			text: this.badgeCount.toString()
			})

		},
		update(){
			this.setbadgeCount();









			

			wx.setTabBarBadge({
			index: 2,
			text: this.badgeCount.toString()
			})

		},
		ToMessage(){
			uni.navigateTo({
				url:'message/message',
			})
		}
	},
	
}



